<template>
  <div>
    <navMedic/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Reporte de Internaciones</b>
      </h1>
      <h4 class="text-center">del Médico {{medic.nombre}} {{medic.apellido}}</h4>
      <div v-for="(internacion, key) in medic.internaciones" :key="key">
        <fieldset class="container mb-2 mt-2">
          <b-row class="p-3">
            <b-col class="shadow-sm">
              <ul>
                <legend><h4>Paciente</h4></legend>
                <hr>
                <p>
                  <b>Paciente número:</b>
                  {{key+1}}
                </p>
                <li>
                  <p>
                    <b>Nombres: </b>
                    <span>{{ internacion.paciente.nombre }} {{ internacion.paciente.apellido }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Numero de Documento: </b>
                    <span>{{ internacion.paciente.numDocumento }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Domicilio: </b>
                    {{ internacion.paciente.domicilio }}
                  </p>
                </li>
                <li>
                  <p>
                    <b>Obra Social: </b>
                    {{ internacion.paciente.obraSocial }}
                  </p>
                </li>
              </ul>
            </b-col>
            <b-col class="shadow-sm">
              <ul>
                <legend><h4>Internación</h4></legend>
                <hr>
                <p>
                  <b>Internacion número:</b>
                  {{key+1}}
                </p>
                <li>
                  <p>
                    <b>Estado: </b>
                    <span
                      v-if="internacion.estadoInternacion.descripcion==='En progreso' ? true : false"
                      class="text-success"
                    >{{ internacion.estadoInternacion.descripcion }}</span>
                    <span v-else class="text-danger">{{ internacion.estadoInternacion.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Motivo: </b>
                    <span>{{ internacion.motivo }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Fecha de Ingreso: </b>
                    {{convertDateToString(internacion.fechaInternacion)}}
                  </p>
                </li>
              </ul>
            </b-col>
            <b-col class="shadow-sm">
              <ul>
                <legend><h4>Habitación</h4></legend>
                <hr>
                <li>
                  <p>
                    <b>Piso: </b>
                    <span>{{ internacion.habitacion.piso.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Tipo: </b>
                    <span>{{ internacion.habitacion.tipoHabitacion.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Género: </b>
                    <span>{{ internacion.habitacion.genero.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Número de Habitación: </b>
                    <span>{{ internacion.habitacion.numero }}</span>
                  </p>
                </li>
              </ul>
            </b-col>
          </b-row>
        </fieldset>
      </div>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navMedic from "@/components/medic/navMedic.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "internacionesDeMedico",
  data() {
    return {
      id: this.$route.params.id,
      medic: []
    };
  },
  methods: {
    async findById() {
      axios
        .get(`${URL}/api/medico/${this.id}`)
        .then(response => (this.medic = response.data))
        .catch(error => {
          bootbox.alert({
            title: "Error",
            message: error.response.data.mensaje,
            backdrop: true,
            locale: "es",
            callback: this.$router.push("/medicos")
          });
          console.log("Error: " + error.response.status + " Not Found");
        });
    },
    convertDateToString(date) {
      let dateObject = new Date(date);
      let formatter = new Intl.DateTimeFormat("es", {
        weekday: "long",
        year: "numeric",
        month: "long",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
        second: "numeric"
      });
      return formatter.format(dateObject);
    }
  },
  components: {
    navMedic,
    Footer
  },
  mounted() {
    this.findById();
  }
};
</script>

